@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

.App {
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
  min-width: 100vw;
  background-image: url("../src/images/background.jpg");
  background-size: cover;
  background-position: center;
}

.title {
  text-align: center;
}
#add-metric-button {
  background-color: #f15d22;
  color: white;
  outline: none;
  margin: 2.5em 0 1em 0.5em;
}

#add-metric-button:active,
#add-metric-button:focus,
#submit-metric-button:active,
#submit-metric-button:focus {
  outline: none;
  box-shadow: 0 0 0 0;
}
.error-message {
  color: red;
  font-size: 0.9em;
  position: relative;
  bottom: 1em;
  left: 1em;
}
#add-metric-form {
  display: flex;
  margin-bottom: 2.5em;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.table-header {
  cursor: pointer;
}
#submit-metric-button {
  background-color: #f15d22;
  margin: 1em 0 0 0;
  color: white;
  outline: none;
  width: fit-content;
  text-align: center;
  align-self: center;
}
.label-wrapper {
  margin-bottom: 1.5em;
}
.tspsns {
  margin-bottom: 1.5em;
}
.tpfntnfn {
  margin-bottom: 1.5em;
}
th {
  text-align: center;
}
.radar-wrapper {
  height: 30em;
  padding: 2em;
}
#methods-table {
}
.confusion-matrix-wrapper {
  background-color: rgb(0, 0, 0);
  border-radius: 0.2em;
  text-align: center;
}
.method-label {
  font-size: 1.3em;
  color: white;
}
.confusion-matrix {
  background-color: white;
  height: 19em;
  width: 19em;
  display: grid;

  grid-template-columns: 50% 50%;
  grid-template-rows: 50% 50%;
}
.matrix-cell {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 350;
  font-size: 1.9em;
}
.component-label {
  font-weight: 400;
}
.green {
  background-color: #e1fddc;
}
.red {
  background-color: #f8a9a8;
}
.settings {
  display: flex;
  justify-content: space-around;
}
.label-cell {
  cursor: pointer;
}
.label-cell-focus {
  cursor: pointer;
  background-color: #b8e6db;
}
.edit-method,
.delete-method {
  background-color: transparent;
  border: none;
}

.form-input {
  border-radius: 0.16rem;
  border: 0.04em solid rgb(105, 102, 102);
  height: 1.8em;
  outline: none;
  padding-left: 0.5em;
  width: 11em;
}
.input-wrapper {
  width: 22em;
  margin-top: 0.1em;
  padding-left: 1em;
  display: flex;
}
.intro {
  font-weight: 600;
  font-size: 1.4em;
  width: 100%;
  padding: 0 2.5em 0 2.5em;
  text-align: center;
}
.brand {
  display: flex;
  justify-content: center;
  margin-top: 1em;
  width: 100%;
}
.brand-logo {
  height: 4.1em;
  width: 4.1em;
  position: relative;
  bottom: 0.4em;
  left: 0.2em;
}
footer {
  background-color: #f15d22;
  display: flex;
  font-size: 0.75em;
  justify-content: center;
  align-items: center;
  color: white;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 2.6em;
  width: 100vw;
}
.github-repo {
  cursor: pointer;
  position: relative;
  left: 1.5em;
}

a,
a:hover {
  color: white;
}
label {
  font-weight: 600;
  width: 48%;
}
.input-conflict {
  margin-left: 0.5em;
  background-color: #ff9201;
  border-radius: 2em;
  padding: 0.2em 0.4em 0.2em 0.4em;
}
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
}
.negative-error {
  border: 0.08em solid #d53a47;
}
.error-messages {
  text-align: justify;
  list-style: none;
  color: #d53a47;
}
.required-icon {
  color: red;
  font-size: 0.4em;
  position: relative;
  bottom: 1.1em;
  left: 0.2em;
}
.new-method-title {
  text-align: center;
  margin-top: 0.45em;
  margin-left: 0.3em;
}

.commands,
.commands:active,
.commands:focus {
  border: none;
  outline: none;
  background-color: transparent;
}
.sort-icon {
  position: relative;
  left: 0.5em;
}
.matrix-header {
  padding: 0 0.5em 0 0.5em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.close-matrix {
  color: white;
  cursor: pointer;
}
.tooltiped {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.form-tooltip {
  position: relative;
  right: 0.27em;
}
.custom-tooltip {
  max-width: 400px !important;
}
